@import '../Base/variables';

#about{
  padding: 1rem;

  #about__headerText{
    text-align: center;
    margin: 3rem 0rem;
    text-transform: uppercase;
    font-size: 2rem;
  }

  #about__headerTextSecondary{
    color: $secondary-font-color;
  }

  #about__container{
    display: flex;
    align-items: center;
    justify-content: space-between;

    #about__text{
      width: 50%;
    }
  }
}
